import React, {Component} from "react";
import PropTypes from "prop-types";

export const ResponsiveVisibility = (props) => {
    let tablet = props.tablet === undefined ? props.desktop : props.tablet;
    let mobile = props.mobile === undefined ? props.desktop : props.mobile;
    let big = props.desktop ? "" : "u-hide ";
    let medium = tablet ? (props.desktop ? "" : "u-medium-block") : (props.desktop ? "u-medium-hide" : "");
    let small = mobile ? (props.desktop ? "" : " u-small-block") : (props.desktop ? " u-small-hide" : "");
    return (
        <div className={big+medium+small}>
            {props.children}
        </div>
    )
}

ResponsiveVisibility.propTypes = {
    desktop: PropTypes.bool,
    tablet: PropTypes.bool,
    mobile: PropTypes.bool
}

ResponsiveVisibility.defaultProps = {
    desktop: true
}